<template>
  <div id="videoPlay">
    <meta name="referrer" content="never">
    <d-player ref="player" id = "player" :options="options" muted></d-player>
  </div>
</template>

<script>
import VueDPlayer from 'vue-dplayer'
import 'vue-dplayer/dist/vue-dplayer.css'

export default {
  name: 'videoPlay',
  props:["path"],
  components: {
    'd-player': VueDPlayer
  },
  data() {
    return {
      player: null,
      options: {
        // autoplay: true,
        // container: document.getElementById("dplayer"),

        // mutex: false, //  防止同时播放多个用户，在该用户开始播放时暂停其他用户
        // theme: "#b7daff", // 风格颜色，例如播放条，音量条的颜色
        // muted:true,
        loop: true, // 是否自动循环
        // lang: "zh-cn", // 语言，'en', 'zh-cn', 'zh-tw'
        // screenshot: true, // 是否允许截图（按钮），点击可以自动将截图下载到本地
        // hotkey: true, // 是否支持热键，调节音量，播放，暂停等
        // preload: "auto", // 自动预加载
        // volume: 0.7, // 初始化音量

        video: {
          // url: ''
          url: 'https://api.dogecloud.com/player/get.mp4?vcode=5ac682e6f8231991&userId=17&ext=.mp4'
        },
        contextmenu: [
          {}
        ],
      },
    }
  },
  mounted() {

  },
  created() {

  },
  methods: {
    getVideoPlay(playhtml) {
      this.player = this.$refs.player.dp
      this._setVideoUrl(playhtml)
    },
    // 设置视频播放路径
    _setVideoUrl(url) {
      this.player.switchVideo({
        url: url
      })
    }
  },
}
</script>

<style scoped lang="less">
*{
  padding: 0;
  margin: 0;
}
#videoPlay {
  width: 100%;
  // height: 700px;
  height: 50vh;
  // margin: 0 auto;
  text-align: center;

  /deep/ video{
    object-fit: scale-down;
    
  }

  /deep/ .dplayer {
    .dplayer-video{
      width: 100%;
      height: 40vh;
    }
    
    .dplayer-controller {
      .dplayer-icons{
        height: 34px;
        .dplayer-play-icon{
          padding:0;
          padding-top: 2px;
          svg{
            width: 26px;
            height: 21px;
          }

        }
        .dplayer-volume{
          .dplayer-volume-icon{
            width: 26px;
            height: 21px;
            padding:0;
          }
        }

      }
      .dplayer-icons-right{
        right: 2px;
        height: 32px;

        .dplayer-setting{
          .dplayer-setting-icon{
            width: 35px;
            height: 35px;
          }
        }

        .dplayer-full{
          .dplayer-full-icon{
            width: 35px;
            height: 35px;
          }

        }

      }
    }
  }


}
</style>